/* Cómo funciona animaciones
/*––––––––––––––––––––––––––––––––––––––––––––––*/
.iframe-wrapper{
	margin-top: -140px;
	display: flex;
	justify-content: center;
	overflow: hidden;
}//iframe-wrapper


.como-funciona--mobile{
	width: 100%;
	height: 1470px;
	display: block;
	// margin-right: -35px;
	// margin-bottom: -15px;
	overflow: hidden;
}


.como-funciona--small{
	width: 768px;
	height: 3200px;
	margin-right: -35px;
	margin-bottom: -15px;
	display: none;
	overflow: hidden;
}//como-funciona--small


.como-funciona--desktop{
	width: 1200px;
	height: 1300px;
	margin-right: -35px;
	margin-bottom: -15px;
	display: none;
	overflow: hidden;
}//como-funciona--desktop




@media (min-width: 768px) {
	.como-funciona--small{
		display: block;
	}

	.como-funciona--mobile{
		display: none;
	}
}




@media (min-width: 1200px) {
	.como-funciona--desktop{ display: block; }
	.como-funciona--small{ display: none; }
}


