/*-------------------------------------------------*/
/*	BLOG
/*-------------------------------------------------*/
.blog-wrapper{


	.post-thumbnail{
		border-top: 8px solid $green;
		margin-bottom: 30px;
		overflow: hidden;

		img{
			width: 100%;
			height: 100%;
			object-fit: cover;
		}//img
	}//post-thumbnail



	.post-title{
		font-size: 1.6rem;
		line-height: 1;

		a{
			color: inherit;

			&:hover, &:active, &:focus{
				color: #000;
			}
		}
	}



	.nav.post-categories{
		font-size: .7rem;
		font-weight: 600;
		padding-bottom: 5px;

		.nav-item{
			margin-right: 5px;
		}

		.nav-link{
			color: #000;
			text-transform: uppercase;
			padding: 2px;
		}
	}//cat-links



	.post-date{
		a{
			color: #ccc;
			font-weight: 700;
			font-size: 0.8rem;
			text-transform: uppercase;
			letter-spacing: .5px;
		}
	}//post-date




	.btn-link{
		background: url('./assets/images/long-arrow-r.svg') no-repeat 99% center / 30px;
		color: #000;
		text-align-last: left;
		width: 50%;
		padding: 10px 0;
		display: block;



		&:hover, &:active, &:focus{
			background-position: 100% center;

		}
	}



	#blog-pagination{
		text-align: center;

		.nav{
			display: flex;
			justify-content: center;

			a{
				background: #000;
				color: #fff;
				font-size: 0.9rem;
				font-weight: 600;
				padding: 12px 50px;
				margin: 0 6px;
				border-radius: 1000px;

				&:hover, &:active, &:focus{
					text-decoration: none;
				}
			}//a
		}//nav
	}//posts-nav


}//blog-wrapper








