/*-------------------------------------------------*/
/*	POST
/*-------------------------------------------------*/
.post-wrapper{

	.post-title{
		font-weight: 900;
		line-height: 1;
		margin-top: -80px;
		margin-bottom: 50px;
	}

	article{
		padding-left: 40px;
	}

	.meta{
		padding: 0 0 50px;
		display: flex;
		justify-content: space-between;
		align-items: center;

		.post-date{
			a{
				color: #000;
			}
		}//post-date

		.share{
			a{
				color: $green;
				padding: 10px;
				font-size: 17px;
				line-height: 17px;

				&:hover, &:active, &:focus{
					color: darken($green, 6%);
				}
			}
		}//share
	}//meta




	.post-content{

		h1, h2, h3, h4, h5{
			font-weight: 800;
		}

		a{
			color: #000;
			text-decoration: underline;
		}

		img{
			max-width: 100%;
		}
	}//post-content



	.post-info{

		strong, a{
			color: #000;
			font-weight: 600;
			font-style: italic;
		}
		
		.nav{
			display: inline-block;

			.nav-link{
				color: #000;
				font-weight: 600;
				font-style: italic;
				padding: 0;
				display: inline-block;
			}
		}
	}//post-info


	.separator{
		border-bottom: solid 2px #000;
		padding: 40px 0;
	}//separator


	.goback{
		background: url('assets/images/long-arrow-l.svg') no-repeat center left / 26px;
		color: #000;
		font-weight: 600;
		padding-left: 100px;
	}//goback







	/*-------------------------------------------------*/
	/*	COMMENTS
	/*-------------------------------------------------*/
	.comments-wrapper{
		padding: 60px 0;

		header{
			padding-bottom: 30px;
			h5{
				font-weight: 600;
			}
		}

		.form-control{
			border: solid 2px #000;
		}

		textarea{
			resize: none;
		}

		.btn.btn-primary{
			padding-left: 60px;
			padding-right: 60px;
		}

		a:not(.btn){
			color: #000;
			font-weight: 600;
		}
	}//comments-wrapper



	.comment-list{
		padding: 0;

		li{
			list-style: none;

			article{
				padding: 0;
				padding-bottom: 40px;
			}
		}

		.comment-body{
			display: flex;
			flex-flow: row wrap;
		}//comment-body

		.comment-meta{
			padding-right: 20px;
			flex: 1 10%;

			.vcard{
				img{
					width: 90px;
					height: 90px;
					margin-bottom: 15px;
				}

				.fn{
					font-size: .9rem;
					line-height: 1.2;
					display: block;
					padding-bottom: 15px;

					a{
						font-weight: 800;
					}
				}//fn

				.says{ display: none; }

			}

			.comment-metadata{
				font-size: 0.7rem;
			}

			.edit-link{
				margin-top: 20px;
				display: block;
				
				a{
					background: #000;
					color: #fff !important;
					padding: 3px 15px;
					display: inline-block;
					text-transform: uppercase;
				}
			}
		}//comment-meta


		.comment-content{
			flex: 1 80%;
		}

		.reply{
			padding: 10px 0 20px;
			border-bottom: solid 1px #ccc;
			flex: 1 100%;
			text-align: right;
			margin-bottom: 10px;
		}


		.children .comment-meta{
			flex: 1 20%;

			.vcard img{
				width: 70px;
				height: 70px;
			}
		}//comment-meta

	}//comment-list
	


}//post-wrapper





