/*---------------------------------------------------------*/
/*	#41: Hacer un popup sobre el botón "me interesa" 
/*	de programa de afiliados
/*---------------------------------------------------------*/
.popup-wrapper{
	background: rgba(#000,.1);
	left: 0;
	right: 0;
	top: 0;
	bottom: 0;
	position: fixed;
	z-index: 99;
}//popup-wrapper


.popup{
	background: #000;
	color: #fff;
	max-width: 300px;
	padding: 30px;
	margin-left: -30px;
	position: absolute;
	transform: translate(-10px,-50px);
	opacity: 0;
	visibility: hidden;
	transition: .4s;
	z-index: 100;


	a{
		color: $green;
	}


	.close{
		background: url('./assets/images/close-x.svg') no-repeat left bottom / 14px;
		width: 24px;
		height: 24px;
		right: 0;
		top: 0;
		position: absolute;
	}

	&.in{
		opacity: 1;
		visibility: visible;
	}
}//popup





