/*---------------------------------------------------------*/
/*	#55: Cambios en el widget
/*	2018 11 19 - @fitodac
/*---------------------------------------------------------*/
#widget{

	.evaluation{

		.slider{
			padding-bottom: 30px;

			.slider-container .scale span ins{
				background: #f1f1f1;
				color: #c2c2c2;
				padding: 6px;
				left: auto;
				right: 0;
				top: -45px;
				border-radius: 3px;
			}

			[type=number]+span{
				font-size: 2.2rem;
				line-height: 1;
				position: relative;
				top: 5px;
			}
		}//slider


		footer{
			margin-bottom: 50px;

			.h5{
				padding-left: 30px;
			}

			.info{
				font-size: 1.1rem;
				line-height: 1;
				font-weight: 400;
				padding: 6px 30px 10px;
				display: block;

				> div{
					display: flex;
					justify-content: space-between;
				}

				.title{
					color: #000;
					font-weight: 500;
					padding-bottom: 6px;
					display: block;
				}

				.desc{
					color: #7b7b7b;
				}

				.value{
					font-size: 1.1rem;
					font-weight: 300;
				}
			}//info

			.info.first-of-type{
				padding-top: 30px;
			}

			.info.last-of-type{
				padding-bottom: 30px;
			}

		}//footer

	}//evaluation



	.stats{
		margin: 0;
		padding: 0 0 30px;

		span{
			color: #000;
		}

		.value{
			color: #57b991;
			font-size: 1.4rem;
			font-weight: 400;
		}

		.info-progress{ position: relative; }

		.info-progress .progress{
			margin-top: 15px;
		}

		.info-progress small{
			content: '100%';
			background: #f1f1f1;
			color: #c2c2c2;
			line-height: 1;
			padding: 6px;
			left: auto;
			right: 0;
			top: 0;
			position: absolute;
			border-radius: 3px;
		}
	}//stats




	.property{
		height: 100%;
		// padding-left: 80px;
		display: flex;
		flex-flow: column wrap;



		.slide{
			width: 100%;
			max-height: 220px !important;
			margin-bottom: 25px;
			flex: 1;
			overflow: hidden;
			position: relative;

			.owl-item > div{
				height: 100%;

				img{
					width: 100%;
					height: 100%;
					object-fit: cover;
				}
			}//owl-item


			.owl-nav{
				// background: red;
				left: 0;
				right: 0;
				top: 0;
				bottom: 0;
				position: absolute;
				pointer-events: none;
				opacity: 0;
				visibility: hidden;
				transition: .3s;
				z-index: 30;


				button{
					background-repeat: no-repeat;
					background-size: 20px;
					color: #fff;
					width: 40px;
					height: 100%;
					font-size: 0;
					line-height: 1;
					position: absolute;
					transition: .3s;
					pointer-events: initial;
					overflow: hidden;
				}//button

				.owl-prev{ background-image: url('assets/images/angle-left.svg'); background-position: 100% center; left: 0; }
				.owl-next{ background-image: url('assets/images/angle-right.svg'); background-position: 0% center; right: 0; }

				.owl-prev:hover{ background-position: center; }
				.owl-next:hover{ background-position: center; }
			}//owl-nav


			&:hover .owl-nav{
				opacity: 1;
				visibility: visible;
			}//:hover owl-nav

		}//slide



		.title{
			font-size: 2rem;
			font-weight: 800;
		}//title

		.address{
			padding-bottom: 15px;
		}

		.files{
			.title{
				font-size: 1.2rem;
				margin-bottom: 15px;
			}

			a{
				background: url('assets/images/link.svg') no-repeat center left / 13px;
				color: #000;
				white-space: nowrap;
				padding-left: 20px;
				margin-bottom: 10px;
				display: inline-block;
			}//a
		}//files
	}//property


	.btn-xs-big{
		font-size: 1.2rem;
		font-weight: 800;
		letter-spacing: 0;
		padding-top: 15px;
		padding-bottom: 15px;
	}//btn-xs-big


