/*----------------------------------------*/
/*	POPUP OPPORTUNITIES
/*----------------------------------------*/
.popup-oportunities-wrapper{
	background: rgba(#000,.5);
	align-items: center;
	justify-content: center;
	display: none;
	left: 0;
	right: 0;
	top: 0;
	bottom: 0;
	position: fixed;
	opacity: 0;
	visibility: hidden;
	transition: .4s;
	z-index: 9999;


	&.in{
		display: flex;
		opacity: 1;
		visibility: visible;
	}



	.popup-oportunities{
		background: url('assets/images/popup-bg-1.svg') no-repeat center bottom / cover;
		width: 700px;
		padding: 50px 50px 70px;
		border-radius: 15px;
		position: relative;


		.title{
			font-size: 1.1rem;
			font-weight: 500;
			margin-bottom: 20px;
		}


		.content{
			font-size: 0.9rem;
			margin-bottom: 30px;

			a{
				color: inherit;
				text-decoration: underline;
			}
		}//content


		.footer{
			font-size: 0.9rem;
			font-weight: 500;

			a{
				color: inherit;
				text-decoration: underline;
			}


			label{
				position: relative;
				padding-left: 32px;

				[type=checkbox]{
					left: 0;
					top: 0;
					position: absolute;
					opacity: 0;
					visibility: hidden;
				}

				[type=checkbox] + span{
					background: transparent;
					border: solid 2px #000;
					width: 22px;
					height: 22px;
					left: 0;
					top: 0;
					position: absolute;
					display: block;
					cursor: pointer;
				}

				[type=checkbox]:checked + span{
					background: url('assets/images/x.png') no-repeat center / cover;
				}
			}//label
		}//footer


		.close{
			font-size: 60px;
			font-weight: 300;
			line-height: 1;
			text-align: center;
			width: 60px;
			height: 60px;
			right: 10px;
			top: 10px;
			position: absolute;
			text-shadow: none;
			opacity: 1;
		}

	}//popup-oportunities


}//popup-oportunities-wrapper